
body{
    background-color: #EFEFEF;
    margin: 0;
    font-family: "Mulish";
}
.content-body{
    margin-top: 40px;
    padding: 0 30px 0 30px;
}
p{
    margin: 0;
}
.un-fade{
    opacity: 0 !important;
    pointer-events: none;
}

.d-none{
    display: none !important;
}

.fade{
    opacity: 1;
}

.invalid{
    background-color: gray !important;
    pointer-events: none;
}

.title{
    font-weight: 600;
    font-size: 14px;
}

select{
    height: 40px;
    border-color: #d9e1e2;
    width: 100%;
    margin-bottom: 10px;
    padding: 0 8px;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
}